@import url(//fonts.googleapis.com/earlyaccess/notosanskr.css);
.nav {
    z-index: 1;
    position: fixed;
    top: 10px;
    left: 10px;
    display: flex;
    flex-direction: column;
    padding: 10px 20px;
}
@media screen and (max-width: 1090px) {
    .nav {
        left:-20px;
        top: auto;
        top: initial;
        bottom: 20px;
        width: 100%;
    }
}
    .nav a {
        opacity:50%;
        text-decoration: none;
        color: #0008fc;
        text-transform: uppercase;
        font-size: 12px;
        text-align: center;
        font-weight: 600;
    }
        .nav a:not(:last-child) {
            margin-bottom: 20px;
        }


.home_doorText {
    animation: fadein 4s;
    font-size: 40px;
}
    .home_doorText a {
        -webkit-text-decoration-line: none;
                text-decoration-line: none;
        color: #000000;
    }
    .home_doorText a:hover {
        color: #0e3b44;
    }

.home_door {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 600px;
    height: 300px;
    box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25), 0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
    border-radius: 30px;
    background-color: rgba( 255, 255, 255, 0.7 );
}


@keyframes fadein {
    from {
        opacity: 0;
    }

    to {
        opacity: 1;
    }
}

.clubrooom__container {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 700px;
    height: 350px;
    box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25), 0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
    border-radius: 30px;
    background-color: rgba( 255, 255, 255, 0.7 );
}

    .clubrooom__container > img {
        width: 700px;
        height: 350px;
        border-radius: 30px;
    }
.password__container {
    display: grid;
    grid-template-rows:1fr 2fr;
    width: 600px;
    height: 300px;
    place-items:center;
}
    .password__container > h1 {
    }
    .password__container > form {
        display: grid;
        grid-template-rows: 120px 80px;
    }
.textBoxes {
    display: grid;
    grid-template-columns: repeat(6,60px);
    padding-top:10px;
    grid-column-gap:10px;
    column-gap:10px;
    height:80px;
}
    .textBoxes > input {
        box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25), 0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
        border-radius: 3px;
        background-color: rgba( 255, 255, 255, 0.7 );
        border-style:hidden;
        text-align: center;
        font-size: 40px;
    }
.password__action {
    margin:auto;
}
.password__button {
    width: 50px;
    height: 30px;
    text-decoration: none;
    box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25), 0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
    border-radius: 3px;
    background-color: rgba( 255, 255, 255, 0 );
    border-style: hidden;
    text-align: center;
    color: #ffffff;
    opacity:70%;
}
.password__action>input[type=reset]{
    margin-left:10px;
}
body {
    color: #fff;
    background: linear-gradient(-45deg, #ee7752, #e73c7e, #23a6d5, #23d5ab);
    background-size: 400% 400%;
    animation: backgroundChange 15s ease infinite;
    width: 100vw;
    min-width: 900px;
    height: 100vh;
    overflow: hidden;
    font-family: NanumSquare;
    color : #ffffff;
}


.container {
    width: 100vw;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: 300;
}

@keyframes backgroundChange {
    0% {
        background-position: 0% 50%
    }

    50% {
        background-position: 100% 50%
    }

    100% {
        background-position: 0% 50%
    }
}


